import React, { Component, Fragment } from 'react';
import { withRouter, Link } from 'react-router-dom';
import { Card, Row, Table, Col, message } from 'antd';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { getComeFrom, deleteComeFrom } from 'services/comeFrom';
import routeConstant from 'routes/route/routeConstant';
import { ColEditDelete } from 'components/common';
import './style.scss';

class ComeFrom extends Component {
  constructor(props) {
    super(props);
    this.handleDelete = this.handleDelete.bind(this);
    this.state = {
      comeFrom: [],
      loading: true,
    };
    this.title = 'Danh mục xuất thân';

    this.columns = [
      {
        title: 'Mã xuất thân',
        dataIndex: 'id',
        key: 'id',
        // sorter: (a, b) => a.townId.length - b.townId.length,
        defaultSortOrder: 'descend',
      },
      {
        title: 'Tên xuất thân',
        dataIndex: 'label',
        key: 'label',
        // sorter: (a, b) => a.label.length - b.label.length,
        defaultSortOrder: 'descend',
      },
      {
        title: 'Tên tiếng anh',
        dataIndex: 'labelInEnglish',
        key: 'labelInEnglish',
        // sorter: (a, b) => a.labelInEngLish.length - b.labelInEngLish.length,
        defaultSortOrder: 'descend',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) =>
          this.state.comeFrom.length >= 1 ? (
            <Fragment>
              <ColEditDelete
                linkEdit={{
                  to: {
                    pathname: routeConstant.DANH_MUC__TUYEN_SINH__XUAT_THAN__NHAP_DU_LIEU,
                    search: `?id=${record.id}`,
                  },
                }}
                onConfirmDelete={() => this.handleDelete(record.id)}
              />
            </Fragment>
          ) : null,
      },
    ];
    // rowSelection object indicates the need for row selection
    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
    this.cssBetweenRow = { marginBottom: '8px' };
  }

  componentDidMount = async () => {
    this.getComeFrom();
  };

  onChange = (value, selectedOptions) => {
    console.log(value, selectedOptions);
  };

  getComeFrom = async () => {
    const res = await getComeFrom();
    this.setState({ comeFrom: res.data.items, loading: false });
  };

  handleDelete = id => {
    deleteComeFrom(id).then(res => {
      if (res.status === 200) {
        message.success('Xóa xuất thân thành công.');
        this.getComeFrom();
      } else {
        message.error(res.message);
      }
    });
  };

  render() {
    return (
      <Card title={this.title}>
        <Row>
          <Col span={24}>
            <Link to={routeConstant.DANH_MUC__TUYEN_SINH__XUAT_THAN__NHAP_DU_LIEU}>
              <BtnAddNew />
            </Link>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              loading={this.state.loading}
              rowSelection={this.rowSelection}
              bordered
              dataSource={this.state.comeFrom}
              columns={this.columns}
              onRow={() => ({
                onDoubleClick: () => {},
              })}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}
export default withRouter(props => (
  <Page {...props}>
    <ComeFrom {...props} />
  </Page>
));



